import { Injectable } from '@angular/core';
import {Http,Response} from '@angular/http';
import 'rxjs/add/operator/map';
@Injectable({
  providedIn: 'root'
})
export class StockDataService {
  dataObj;
  dataArr:any;
  constructor(private http:Http) { 
  this.dataArr=['GOLD','fb','isns','vii','goog','cvs','aapl','inpix','gib','EZA','fadtx','dxc'];
  }
  getData(){
    var param = "symbols="+this.dataArr;
    var url="https://angular2-in-action-api.herokuapp.com/stocks/snapshot?"+param;
    return this.http.get(url)
    .map((res:Response)=>this.dataObj=res.json())
  }
}
